import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Heart, Eye, ExternalLink, Zap } from "lucide-react";
import { cn } from "@/lib/utils";

interface NFTCardProps {
    tokenId: string;
    title: string;
    creator: string | undefined;
    price: string;
    views: number;
    contractAddress: string;
    rarity: "Common" | "Rare" | "Epic" | "Legendary";
    liked?: boolean;
    className?: string;
}

const rarityColors = {
    Common: "bg-muted text-muted-foreground",
    Rare: "bg-nft-cyan/20 text-nft-cyan border-nft-cyan/30",
    Epic: "bg-nft-purple/20 text-nft-purple border-nft-purple/30", 
    Legendary: "bg-nft-gold/20 text-nft-gold border-nft-gold/30"
};

export function NFTCard({ 
    tokenId, 
    title, 
    creator, 
    price, 
    image, 
    liked = false, 
    views, 
    rarity,
    className 
}: NFTCardProps) {
    const [isLiked, setIsLiked] = useState(liked);
    const [isHovered, setIsHovered] = useState(false);

    return (
        <Card 
            className={cn(
                "group relative overflow-hidden rounded-2xl border-0 p-0",
                "bg-gradient-card backdrop-blur-xl",
                "hover:shadow-nft transition-all duration-500 ease-out",
                "animate-float",
                className
            )}
            onMouseEnter={() => setIsHovered(true)}
            onMouseLeave={() => setIsHovered(false)}
            >
            {/* Glow effect */}
            <div className="absolute inset-0 rounded-2xl bg-gradient-glow opacity-0 group-hover:opacity-20 transition-opacity duration-500 blur-xl" />
            
            {/* Shimmer effect */}
            <div className="absolute inset-0 rounded-2xl overflow-hidden">
            <div 
            className={cn(
                "absolute inset-0 translate-x-[-100%] bg-gradient-to-r from-transparent via-white/10 to-transparent",
                "group-hover:animate-shimmer transition-transform duration-1000"
            )}
            />
        </div>

        {/* Main content */}
        <div className="relative z-10">
            {/* Image container */}
            <div className="relative aspect-[4/3] overflow-hidden rounded-t-2xl">
            <img
                src={image}
                alt={title}
                className={cn(
                "h-full w-full object-cover transition-all duration-700",
                "group-hover:scale-110",
                isHovered && "animate-glow"
                )}
            />
            
            {/* Overlay gradient */}
            <div className="absolute inset-0 bg-gradient-to-t from-background/60 via-transparent to-transparent" />
            
            {/* Top badges */}
            <div className="absolute top-4 left-4 right-4 flex justify-between items-start">
                <Badge 
                className={cn(
                    "px-3 py-1 text-xs font-medium border",
                    rarityColors[rarity]
                )}
                >
                {rarity}
                </Badge>
                <div className="flex gap-2">
                <Button
                    size="sm"
                    variant="secondary"
                    className="h-8 w-8 p-0 rounded-full bg-background/20 backdrop-blur-sm border-white/10 hover:bg-background/40"
                    onClick={() => setIsLiked(!isLiked)}
                >
                    <Heart className={cn("h-4 w-4", isLiked && "fill-nft-pink text-nft-pink")} />
                </Button>
                <Button
                    size="sm"
                    variant="secondary"
                    className="h-8 w-8 p-0 rounded-full bg-background/20 backdrop-blur-sm border-white/10 hover:bg-background/40"
                >
                    <ExternalLink className="h-4 w-4" />
                </Button>
                </div>
            </div>

            {/* Bottom stats */}
            <div className="absolute bottom-4 left-4 flex items-center gap-2 text-white/80">
                <Eye className="h-4 w-4" />
                <span className="text-sm font-medium">{views.toLocaleString()}</span>
            </div>
            </div>

            {/* Card content */}
            <div className="p-6 space-y-4">
            {/* Title and creator */}
            <div>
                <h3 className="text-xl font-bold text-foreground mb-1 line-clamp-1">
                {title}
                </h3>
                <p className="text-muted-foreground text-sm">
                by <span className="text-nft-cyan font-medium">{creator}</span>
                </p>
            </div>

            {/* Price and buy button */}
            <div className="flex items-center justify-between">
                <div>
                <p className="text-xs text-muted-foreground uppercase tracking-wide">
                    Current Price
                </p>
                <p className="text-2xl font-bold bg-gradient-nft bg-clip-text text-transparent">
                    {price} ETH
                </p>
                </div>
                
                <Button 
                className={cn(
                    "bg-gradient-nft hover:shadow-glow transition-all duration-300",
                    "px-6 py-2 rounded-xl font-semibold",
                    "transform hover:scale-105",
                    "border border-nft-purple/30"
                )}
                >
                <Zap className="h-4 w-4 mr-2" />
                Buy Now
                </Button>
            </div>
            </div>
        </div>
        </Card>
    );
}